* {
  font-family: sans-serif;
}

.App {
  text-align: center;
  /* background-color: #282c34; */
}

input,
button {
  font-size: 20px;
  padding: 10px;
  border-radius: 5px;
}

input {
  display: block;
  margin: 50px auto;
  transition: 0.3s;
  outline: 1px solid royalblue;
  border: none;
}

input:focus {
  outline: 3px solid royalblue;
  border: none;
}

button {
  border: 1px solid gray;
  background: whitesmoke;
  margin-right: 5px;
}

button:nth-last-child(2),
button:nth-last-child(1) {
  background: tomato;
  color: white;
}